.container {
  width: 1158px;
  margin: 0 auto;
  padding: 0 15px;
}

.list {
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 0;
  padding: 15px;
  text-decoration: none;
  display: flex;
}

.item-links {
  text-decoration: none;
  display: inline-block;
}

.link {
  color: #021915;
  padding: 10px 5px;
  text-decoration: none;
}

.action-click {
  font-weight: 900;
}

.link:hover {
  background-color: #fff;
  transform: scale(1.05);
}

.search-form {
  padding: 15px;
}

.fixed-form {
  width: 1158px;
  z-index: 1000;
  background-color: #11ac90;
  border-radius: 2px;
  justify-content: space-between;
  display: flex;
  position: fixed;
  top: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.input {
  width: 300px;
  height: 40px;
  color: #021915;
  border: 1px solid #175f52;
  border-radius: 4px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
}

.button {
  color: #f2faf8;
  letter-spacing: .04em;
  background-color: #175f52;
  border-radius: 4px;
  padding: 11px 24px;
  font-size: 16px;
  font-weight: 500;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin: 100px 0 50px;
  display: flex;
}

a {
  border-radius: 4px;
  text-decoration: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

a:hover {
  transform: scale(1.05);
}

.info {
  letter-spacing: -.01em;
  color: #021915;
  justify-content: center;
  gap: 10px;
  font-size: 10px;
  display: flex;
}

.gallery_link {
  height: 250px;
  width: calc(25% - 7.5px);
}

.img-card {
  object-fit: cover;
  width: 100%;
  border-radius: 4px 4px 0 0;
  margin-bottom: 10px;
  display: block;
}

.is-hidden {
  display: none;
}

.load-more-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #f2faf8;
  letter-spacing: .04em;
  background-color: #175f52;
  border-radius: 4px;
  margin-bottom: 50px;
  padding: 11px 24px;
  font-size: 16px;
  font-weight: 500;
}

